export default {
  props: {
    list: Array,
    name: String,
    extraClass: String,
  },
  data() {
    return {
      listState: this.list.map(item => ''),
    };
  },
  methods: {
    onListItemClick(index) {
      this.$emit('onFormChange', this.name, this.list[index]);
      this.listState = this.listState.map((item, i) => {
        if (i === index) {
          return 'selected-tag';
        } else {
          return '';
        }
      });
    }
  }
}
